/* General Calendar Styles */
.react-calendar {
    width: 100%;
    max-width: 350px;
    background: none;
    border: none;
    font-family: Arial, sans-serif;
}

.react-calendar__tile {
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    font-size: 16px;
    background: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.react-calendar__tile--now {
    background: rgba(0, 0, 0, 0.1); /* Highlight today */
}

.react-calendar__tile--active {
    background: linear-gradient(135deg, #003cff33, #9b82ff56);
    color: white;
    border-radius: 10px;
}

/* Attendance Colors */
.tile-present {
    background: linear-gradient(135deg, #4caf50, #66bb6a); /* Green for present */
    color: white;
}

.tile-absent {
    background: linear-gradient(135deg, #f44336, #ef5350); /* Red for absent */
    color: white;
}

.tile-maybe {
    background: linear-gradient(135deg, #ff9800, #ffb74d); /* Orange for maybe */
    color: white;
}

.holiday {
    background: linear-gradient(135deg, #2196f3, #64b5f6); /* Blue for holidays */
    color: white;
}

.react-calendar__month-view__days__day--neighboringMonth {
    color: rgba(0, 0, 0, 0.5); /* Faded days from previous/next month */
}

/* Attendance Buttons */
.attendance-options {
    margin-top: 20px;
    display: flex;
    gap: 10px;
}

.attendance-options button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    color: white;
    transition: background-color 0.3s ease;
}

.present-btn {
    background-color: #4caf50;
  
}

.absent-btn {
    background-color: #f44336;
}

.maybe-btn {
    background-color: #ff9800;
}

.present-btn:hover {
    background-color: #66bb6a;
}

.absent-btn:hover {
    background-color: #ef5350;
}

.maybe-btn:hover {
    background-color: #ffb74d;
}
.attendance-summary ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.attendance-summary li {
    display: flex;
 
    margin-bottom: 10px;
}

/* Align the status to the left side */
.Status {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-grow: 1; /* Take up available space */
    font-size: 16px;
    color: #333;
    
}